body {
    background-color: #FFCC00;
    padding: 20px;
    margin: 0;
   }
   h1, h2, p, ul, li {
    font-family: sans-serif;
   }
   ul.header li {
    display: inline;
    list-style-type: none;
    margin: 0;
   }
   ul.header {
    background-color: #111;
    padding: 0;
   }
   ul.header li a {
    color: #FFF;
    font-weight: bold;
    text-decoration: none;
    padding: 20px;
    display: inline-block;
   }
   .content {
    background-color: #FFF;
    padding: 20px;
   }
   .content h2 {
    padding: 0;
    margin: 0;
   }
   .content li {
    margin-bottom: 10px;
   }
   .active {
    background-color: #0099FF;
   }
   .map-container {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  }
  .marker {
   /*background-image: url('svg/marker.svg');*/
    background-size: cover;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    cursor: pointer;
  }
  
   